<template>
  <div>
    <input type="text" v-model="msg" />
    <button @click="sendMessage">click</button>
    {{ errors }}
    <ul v-for="(i, msgList) in msgLists" :key="i">
      <li>{{ msgList.msg }}</li>
    </ul>
  </div>
</template>
<script>
const { io } = require("socket.io-client");
const SERVER_URL = "http://localhost:1337";
const socket = io(SERVER_URL);

export default {
  name: "chatView",
  components: {},
  data() {
    return {
      sampleData: "",
      msg: "",
      errors: "",
      msgLists: [
        {
          msg: "",
        },
      ],
    };
  },
  setup() {},
  created() {
    socket.on("connection", () => {});
    socket.on("getChat", (data) => {
      console.log(data);
    });
  },
  mounted() {},
  unmounted() {
    socket.emit("disconnection");
  },
  methods: {
    async sendMessage() {
      await socket.emit("chatSend", { msg: this.msg });
      this.msg = "";
    },
  },
};
</script>
<style scoped></style>
